import React, { useState, useEffect, useContext } from "react";
import { useParams } from "react-router-dom";
import { DataContext } from "./Data";
import countries_list from "./CountriesList.json";
import View from './View';
import placeholder from '../images/news_placeholder.png';

function TopHeadlines() {
  const {
    api_host,
    countries,
    categories,
    news_list,
  } = React.useContext(DataContext);
  const [selected_country, setCountry] = countries;
  const [selected_category, setCategory] = categories;
  var [newsList, setNews] = news_list;

  let slug = useParams();
  useEffect( () =>{
    var country_name;
    setCategory(slug.category_id);
    countries_list && countries_list.length > 0 && countries_list.map((country) => {
      if (country.code == slug.country_id) {
        country_name = country.name;
      }
    });
    setCountry({
      name: country_name,
      code: slug.country_id,
    });
  }, [slug]);
  useEffect(() => {
    const getData = async () => {
      const api_url = `${api_host}/top-headlines/category/${selected_category}/${selected_country.code}.json`;
      const response = await fetch(api_url);
      const data = await response.json();
      setNews(data.articles);
    };
    getData();
  }, [selected_country, selected_category]);

  var newsList1, newsList2;
  {newsList1 = newsList.slice(1, 4)}
  {newsList2 = newsList.slice(4, 10)}
  
  const [news_details, setNewsDetails] = useState([]);
  const current_news_details = (news_object) => {
    setNewsDetails(news_object);
  }

  return (
    <>
      <div className="container">
        <div className="headlines_title">
          Top Headlines from {selected_country.name} on&nbsp;
          <span style={{ textTransform : "capitalize" }}>{selected_category}</span>
        </div>
        {
          <div className="headlines row">
            {newsList1 && newsList1.length > 0 && newsList1.map((news, index) => (
              <div
                key={index}
                className="hd-col col-lg-3 col-md-3 col-sm-6 col-xs-12"
              >
                <div className="hd-card card">
                {index <=2 ? (<img src={news.urlToImage == undefined ? placeholder : news.urlToImage} alt={news.source} />) : 
                ( <></> )}
                  <div className="card-body">
                    <div className="card-title">{news.title}</div>
                    <a href="#" onClick={() => current_news_details(news)}
                     data-toggle="modal" data-target="#exampleModalLong">View More</a>
                  </div>
                </div>
              </div>
            ))}
          </div>
        }
        {
          <div className="headlines2">
            {newsList2 && newsList2.length > 0 && newsList2.map((news, index) => (
              <div key={index} >
                <div className="title">
                    <a href="#" onClick={() => current_news_details(news)}
                     data-toggle="modal" data-target="#exampleModalLong">{news.title}</a>
                </div>
              </div>
            ))}
          </div>
        }
        <View news_details={news_details} /> 
      </div>
    </>
  );
}

export default TopHeadlines;
