import React from "react";

const View = ({ news_details }) => {
  return <div className="card">
<div className="modal fade" id="exampleModalLong" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLongTitle" aria-hidden="true">
  <div className="modal-dialog" role="document">
    <div className="modal-content">
      <div className="modal-header">
        <h5 className="modal-title" id="exampleModalLongTitle">{news_details.title}</h5>
        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div className="modal-body">
        <img src={news_details.urlToImage} />
        {news_details.description}
        <a href={news_details.url} target="_blank" rel="noreferrer">&nbsp;Read More...</a>
      </div>
    </div>
  </div>
</div>
  </div>;
};

export default View;
